export default {
	props: {
		topic: String,
		vip_product: Array
	},
	data() {
		return {
			
		}
	},
	methods: {
		goDetail(index) {
			let id = this.vip_product[index].goods_id
			uni.navigateTo({
				url: '/pages/common/goods-detail/goods-detail?id='+id
			})
		},
		goMoney() {
			uni.navigateTo({
				url: '/pages/components/money/money'
			})
		}
	}
}
